import React, { useState } from 'react';
import styled from 'styled-components';
import './index.css';
import Button from '@mui/material/Button';
import axios from 'axios';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import 'react-notifications/lib/notifications.css';

const Header = styled.div`
  background-color: #e16f3b;
  height: 50px;
`;

const HeaderText = styled.span`
  margin-top: 7px;
  color: white;
  font-size: 22px;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  max-width: 800px;
`;

const App = () => {
  const [data, setData] = useState('');

  const handleClick = (e) => {
    axios.post('https://61b7874164e4a10017d18b87.mockapi.io/questions', {
      data: new Date(),
      message: data
    })
    NotificationManager.success('Питання надіслано, дякуємо');
    setData('')
  };

  return (
    <>
      <Header>
        <Container>
          <HeaderText>Ask a question</HeaderText>
        </Container>
      </Header>
      <Container>
        <textarea
          className='textArea'
          placeholder='Введіть ваше запитання(анонімно)'
          onChange={(e) => setData(e.target.value)}
          value={data}
        ></textarea>
        <Button
          style={{ backgroundColor: '#e16f3b' }}
          variant='contained'
          color='info'
          onClick={handleClick}
          disabled={!data}
        >
          Надіслати
        </Button>
        <NotificationContainer/>
      </Container>
    </>
  );
};
export default App;
